.landingpage_landingBody__3_B10 {
	width: 100%;
	min-height: 100vh;
	margin: 0 auto;
	background: linear-gradient(
		136deg,
		#1f3a93,
		#4484ce,
		#1ad7c0,
		#ff9b11,
		#9b59b6,
		#efefef,
		#000000,
		#ecf0f1
	);
	background-size: 1200% 1200%;
	-webkit-animation: landingpage_AnimationName__1-IlQ 70s ease infinite;
	animation: landingpage_AnimationName__1-IlQ 70s ease infinite;
}
@-webkit-keyframes landingpage_AnimationName__1-IlQ {
	0% {
		background-position: 0% 7%;
	}
	50% {
		background-position: 100% 94%;
	}
	100% {
		background-position: 0% 7%;
	}
}

@keyframes landingpage_AnimationName__1-IlQ {
	0% {
		background-position: 0% 7%;
	}
	50% {
		background-position: 100% 94%;
	}
	100% {
		background-position: 0% 7%;
	}
}
